body, html {
  margin: 0;
  padding: 0;
}

/* Navbar Title */
h2 {
  background-color: hsl(118, 91%, 25%);
  color: white;
  padding: 20px;
  font-size: 24px;
  margin: 0;
  text-align: center; /* Centers text */
  width: 100%; /* Fixes layout issue */
  border-radius: 2px;
}

/* Navbar */
.nav {
  position: fixed; /* Keeps it at the top */
  top: 0;
  left: 0;
  width: 100vw; /* Fixed incorrect 'vb' unit */
  background-color: hsl(118, 91%, 25%);
  color: white;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #444; /* Bottom border instead of top */
}

/* Navigation Links */
.nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-right: 30px;
  display: flex;
  gap: 15px;
}

.nav ul li {
  display: inline; /* Ensures items are in a row */
}

/* Link Styling */
.nav a {
  color: white;
  text-decoration: none;
  font-size: 18px;
  padding: 10px;
}

/* Hover Effect */
.nav a:hover { 
  color: white;
  border-radius: 5px;
  border-bottom: 3px solid hsl(118, 45%, 68%);
  padding-bottom: 5px;
}

.content {
  padding-top: 100px;
}
input
{
  width: 500px;
}